import dayjs from 'dayjs'
import Excel from 'exceljs'
import { uniq, uniqBy } from 'lodash'

import { downloadWorkbook } from '../excel/downloadWorkbook'
import { Api } from '../../data/api'
import { fetchCustomerBoxReportByBoxId } from '../../data/calls'
import { addSingleTableWorksheet } from '../excel/createSingleTableWorkbook'

const samplesColumnConfig: Partial<Excel.Column>[] = [
  { header: 'Sample ID', key: 'sampleId' },
  { header: 'State', key: 'usState' },
  { header: 'Visit ID', key: 'visitId' },
  { header: 'Site Code', key: 'customerSiteCode' },
  { header: 'Sample Date', key: 'sampleDate' },
  { header: 'Latitude', key: 'sampleLatitude' },
  { header: 'Longitude', key: 'sampleLongitude' },
  { header: 'Sample Type', key: 'sampleType' },
  { header: 'Sample Method', key: 'sampleMethod' },
  { header: 'Habitat', key: 'habitatName' },
  { header: 'Area', key: 'area' },
  { header: 'Field Split', key: 'fieldSplit', numFmt: '0.00%' }, // TODO: figure out percentage
  { header: 'Lab Split', key: 'labSplit', numFmt: '0.00%' }, // TODO: figure out percentage
  { header: 'Jar Count', key: 'jarCount' },
  { header: 'Qualitative', key: 'qualitative' },
  { header: 'Mesh', key: 'mesh' },
]

const SAMPLE_ID_COLUMNS = {} // placeholder which is replaced with sample columns

const taxaColumnConfig: Partial<Excel.Column>[] = [
  { header: 'Phylum', key: 'phylum' },
  { header: 'Class', key: 'class' },
  { header: 'Order', key: 'order' },
  { header: 'Family', key: 'family' },
  { header: 'Genus', key: 'genus' },
  { header: 'Species', key: 'species' },
  { header: 'Scientific Name', key: 'scientificName' },
  SAMPLE_ID_COLUMNS,
  { header: 'Total', key: 'total' },
]

export const downloadCustomerBoxReport = async ({
  boxId,
  api,
  filename,
}: {
  boxId: number
  api: Api
  filename: string
}): Promise<boolean> => {
  const {
    boxInfo: {
      submittedBy,
      customer: { organizationName, address1, address2, city, usState, zipCode, phone },
    },
    reportMetrics,
    samples,
    sampleTaxa,
    template,
  } = await fetchCustomerBoxReportByBoxId(api)(boxId)

  // create workbook, worksheet
  const wb = new Excel.Workbook()

  await new Promise<void>((resolve) => {
    const reader = new FileReader()
    reader.onload = async () => {
      const buffer = reader.result
      if (!(buffer instanceof ArrayBuffer)) throw new Error('not an arraybuffer')
      await wb.xlsx.load(buffer)
      resolve()
    }
    reader.readAsArrayBuffer(template)
  })

  // Sheet 1: Summary

  const summaryWs = wb.worksheets[0]
  const findRangeByName = (name: string): string => {
    const found = wb.definedNames.model.find((range) => range.name === name)
    if (!found) throw new Error(`Named range "${name}" not found in workbook.`)
    return found.ranges[0].split('!')[1]
  }
  summaryWs.getCell(findRangeByName('ReportDate')).value = dayjs().format('YYYY-MM-DD')
  summaryWs.getCell(findRangeByName('SubmitterName')).value = submittedBy
  summaryWs.getCell(findRangeByName('CustomerName')).value = organizationName
  summaryWs.getCell(findRangeByName('Address1')).value = address1
  summaryWs.getCell(findRangeByName('Address2')).value = address2
  summaryWs.getCell(findRangeByName('CityStateZip')).value = `${city}, ${usState}  ${zipCode}`
  summaryWs.getCell(findRangeByName('Telephone')).value = phone
  summaryWs.getCell(findRangeByName('Email')).value = 'TODO' // TODO

  // Sheet 2: Samples

  addSingleTableWorksheet(wb, { wsName: 'Samples', columns: samplesColumnConfig, rows: samples })

  // Sheet 3: Taxa

  const sampleIds = samples.map(({ sampleId }) => sampleId)

  const updatedTaxaColumnConfig = [...taxaColumnConfig] // careful not to mutate the original!
  updatedTaxaColumnConfig.splice(
    taxaColumnConfig.findIndex((col) => col === SAMPLE_ID_COLUMNS),
    1,
    ...sampleIds.map((sampleId) => ({ header: `Sample ID ${sampleId}`, key: String(sampleId) }))
  )

  const taxaRowData = uniqBy(sampleTaxa, (taxon) => taxon.taxonomyId).map((row) => ({ ...row })) // one row per taxon
  taxaRowData.forEach((taxonRowData) => {
    let taxonTotal = 0
    sampleIds.forEach((sampleId) => {
      const splitCount = sampleTaxa.find(
        (sampleTaxonData) =>
          sampleTaxonData.taxonomyId === taxonRowData.taxonomyId && sampleTaxonData.sampleId === sampleId
      )?.splitCount
      taxonRowData[String(sampleId)] = splitCount
      taxonTotal += splitCount ?? 0
    })
    taxonRowData['total'] = taxonTotal
  })

  addSingleTableWorksheet(wb, { wsName: 'Taxa', columns: updatedTaxaColumnConfig, rows: taxaRowData })

  // Sheet 4: Metrics

  const reportMetricsColumnConfig = reportMetrics
    .reduce<string[]>((acc, metricsRow) => uniq([...acc, ...Object.keys(metricsRow)]), [])
    .map((columnName) => ({
      key: columnName,
      header: columnName,
    }))

  addSingleTableWorksheet(wb, { wsName: 'Metrics', columns: reportMetricsColumnConfig, rows: reportMetrics })

  // download file

  downloadWorkbook(wb, filename)

  return true
}
