import gql from 'graphql-tag'
import axios from 'axios'
import { BoxInfo as GraphBoxInfo, Sample as GraphSample, SampleTaxa as GraphSampleTaxa } from '@namcbugdb/common'

import { Api } from '../api'
import { ReportDataRow } from '../../types'

export const fetchCustomerBoxReportByBoxIdQuery1 = gql`
  query ($boxId: Int!) {
    boxInfo(boxId: $boxId) {
      submittedBy
      customer {
        organizationName
        address1
        address2
        city
        usState
        zipCode
        phone
        # need email
      }
    }
    # reportMetrics(reportId: ???, boxId: $boxId)
  }
`

export const fetchCustomerBoxReportByBoxIdQuery2 = gql`
  query ($boxId: Int!, $offset: Int!) {
    samples(boxId: $boxId, offset: $offset, limit: 1000) {
      records {
        sampleId
        usState
        visitId
        customerSiteCode
        sampleDate
        sampleLatitude
        sampleLongitude
        sampleType
        sampleMethod
        habitatName
        area
        fieldSplit
        labSplit
        jarCount
        qualitative
        mesh
      }
      nextOffset
    }
  }
`

export const fetchCustomerBoxReportByBoxIdQuery3 = gql`
  query ($boxId: Int!, $offset: Int!) {
    sampleTaxa(boxId: $boxId, offset: $offset, limit: 1000) {
      records {
        taxonomyId
        phylum
        class
        order
        family
        genus
        species
        scientificName
        sampleId
        splitCount
      }
      nextOffset
    }
  }
`

export const fetchCustomerBoxReportByBoxIdQuery4 = gql`
  query ($reportId: Int!, $offset: Int!) {
    reportData(reportId: $reportId, limit: 500, offset: $offset) {
      records
      nextOffset
    }
  }
`

export const fetchCustomerBoxReportByBoxId =
  (api: Api) =>
  async (
    boxId: number
  ): Promise<{
    boxInfo: GraphBoxInfo
    reportMetrics: ReportDataRow[]
    samples: GraphSample[]
    sampleTaxa: GraphSampleTaxa[]
    template: Blob
  }> => {
    const [
      {
        boxInfo,
        // reportMetrics,
      },
      {
        samples: { records: samples },
      },
      {
        sampleTaxa: { records: sampleTaxa },
      },
      template,
    ] = await Promise.all([
      // query 1: boxInfo and reportMetrics
      api.query<{
        boxInfo: GraphBoxInfo
        // reportMetrics: { records: { jsonData: ReportDataRow }[] }
      }>(fetchCustomerBoxReportByBoxIdQuery1, { boxId }),

      // query 2: samples
      api.queryAllPages<{ samples: { records: GraphSample[] } }>(
        fetchCustomerBoxReportByBoxIdQuery2,
        { boxId },
        { dataProperty: 'samples' }
      ),

      // query 3: sampleTaxa
      api.queryAllPages<{ sampleTaxa: { records: GraphSampleTaxa[] } }>(
        fetchCustomerBoxReportByBoxIdQuery3,
        { boxId },
        { dataProperty: 'sampleTaxa' }
      ),

      // fetch: Excel file
      axios
        .get(`${process.env.PUBLIC_URL}/namc_customer_report.xlsx`, { responseType: 'blob' })
        .then(({ data }) => data),
    ])

    return {
      boxInfo,
      reportMetrics: [], // reportMetrics.records.map(({ jsonData }) => jsonData),
      samples,
      sampleTaxa,
      template,
    }
  }
